@font-face {
  font-family: 'Product Sans';
  font-style: normal;
  font-weight: 400;
  
  src: local('Open Sans'), local('OpenSans'), url(https://fonts.gstatic.com/s/productsans/v5/HYvgU2fE2nRJvZ5JFAumwegdm0LZdjqr5-oayXSOefg.woff2) format('woff2');
}

html,body {
  
  background: url(../components/animeBg.jpg);
   background-size:cover!important;
   height: auto !important;
   min-height: 100vh !important;
   max-width: 100%;
   overflow-x:hidden;
   
  }
.blur{
  background: rgba(17, 10, 10, 0.2); 
  background-size: cover !important;
  backdrop-filter: blur(3px);
  min-height:100vh !important;
  height: auto !important;
  width:100% !important;
}

.navbar-color{
  background-color:rgb(64, 82, 243);

  
}
.font-color{
  background-color: rgb(64, 82, 243) !important;
  color: pink !important;
  font-family: 'Product Sans' !important;
  font-size: 30px;
  border-radius: 8px;
  padding:4px;
}
.button-color{
  
  background-color: rgb(64, 82, 243) !important;
  color:pink !important; 
  border-radius: 10px !important;
  font-family: 'Product Sans' !important;
}
.button-color:hover{
  background-color: pink !important;
  color:rgb(64, 82, 243) !important;
}
.singleContainer{
  display:flex;
  flex-direction: column;
}
.singlePaper{
  padding: 15px;
  margin:10px;
  border-radius: 8px;
}
.navBar-iconButton{
  color:rgb(64, 82, 243) !important;
  background-color: pink !important;
  
}
.navBar-searchBar{
     border-radius: 20px !important;
     font-family:'Product Sans' !important;

}
.navBar-form{
   display: flex;
}
.about-Paper{
  margin-top: 180px !important;
  border-radius: 20px !important;
  background: rgb(64, 82, 243) !important;
  padding: 20px !important;
  display: flex;
  flex-direction: row;
  
}
.about-Typo{
  font-family:'Product Sans' !important;
  padding-left: 40px !important;
  color:pink !important;
}
.about-link{
   color: rgb(31, 209, 61) !important;
}
p {
  color: white;
  font: 300 2em/150% Impact;
  text-align: center;}
.loading{
  font-family: 'Product Sans' !important;
  color:pink !important;
  width:550px !important;
    
  
}
.loading:after {
  content: ' .';
  animation: dots 1s steps(5, end) infinite;}

@keyframes dots {
  0%, 20% {
    color: rgba(0,0,0,0);
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);}
  40% {
    color: pink;
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);}
  60% {
    text-shadow:
      .25em 0 0 rgb(209, 189, 189),
      .5em 0 0 rgba(0,0,0,0);}
  80%, 100% {
    text-shadow:
      .25em 0 0 pink,
      .5em 0 0 pink;}}
      
.recommend-paper{
  background-color: rgb(64, 82, 243) !important;
  border-radius: 10px !important;
  width:fit-content!important;
  display: flex;
  flex-direction: row;
}
.random-typo
{
  font-family: 'Product Sans' !important;
  width:fit-content!important;
  color:pink !important;
  background-color:  rgb(64, 82, 243) !important;
  border-radius: 5px;
  justify-content: center !important;
  margin:20px 0  !important;
}
.random-image{
  border-radius: 8px;
}
.listElement{
  background-color: rgb(64, 82, 243) ;
  border-radius: 25px;
  display: flex !important;
  flex-direction: row !important;
  padding: 5px;
  display:block;
  margin: 10px 0 ;
  padding:20px;
  max-width: 550px;
  justify-content: space-between;
}
.listElement:hover{
  transform: scale(1.04);
}
.heading-typo{
   color:pink !important;
   border-radius: 8px !important;
   background-color: rgb(64, 82, 243) !important;
   font-family: 'Product Sans'  !important;
   padding:5px !important;
   
}
.random-buttons >:not(:last-child){
  margin-bottom: 10px !important;
}
.list-buttons{
   min-width: max-content;
}


